.mainBackground {
    background-size: cover !important;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-position: center; 
    background-repeat: no-repeat;
    background-image: url('/src/assets/images/main_background.jpg');
}
.logo {
    max-width: 88vw;
}
.messageContainer {
    background-color: rgba(0,0,0,.74);
    border: 2px solid #56b4ef;
    box-shadow: inset 0 4px 12px rgb(0 0 0 / 5%), 0 0 32px rgb(82 168 236 / 60%);
    max-height: calc(100vh - 20px);
    padding: 20px;
    margin: 10px;
}

.messageTitle {
    color: #fff;
    font-size: 32px;
    font-weight: 700;
    min-height: 30px;
    margin: 30px auto;
    font-weight: bolder;
    text-align: center;
    font-family: beon;
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #b800e6, 0 0 20px #b800e6, 0 0 25px #b800e6, 0 0 30px #b800e6, 0 0 35px #b800e6;
    line-height: 1.3;
    font-size: 25px;
    text-align: center;
}

.message {
    color: #ff00e6;
    font-size: 20px;
    text-align: left;
}

.titleContainer, .buttonsContainer, .logoContainer {
    text-align: center;
}

